.gpt3_possibility
{
    display: flex;
    flex-direction: row;
}

.gpt3_possibility-image
{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}

.gpt3_possibility-image img
{
    width: 90%;
    height: 90%;
}

.gpt3_possibility-content
{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    gap: 1rem;
    margin-bottom: 22px;
}

.gpt3_possibility-content h4
{
    color: #71E5FF;
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
}

.gpt3_possibility-content h4:last-child
{
    color: #FF8A71;
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
}

.gpt3_possibility-content h1
{
    color: #FFF;
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;
}

.gpt3_possibility-content p
{
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 400;
    line-height: 30px;
}

@media screen and (max-width: 950px)
{
    .gpt3_possibility
    {
        flex-direction: column;
    }    

    .gpt3_possibility-image
    {
        margin: 1rem 0;
    }

    .gpt3_possibility-content
    {
        margin-top: 2rem;
    }
}

