.gpt3_blog-container_article
{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}

.gpt3_blog-container_article-image
{
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}

.gpt3_blog-container_article-image img
{
    width: 100%;
    height: 100%;
}

.gpt3_blog-container_article-content
{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}

.gpt3_blog-container_article-content p
{
    color: #FFF;
    font-family: var(--font-family);
    font-size: 11.649px;
    font-weight: 700;
    line-height: 35px;
}

.gpt3_blog-container_article-content h3
{
    color: #FFF;
    font-family: var(--font-family);
    font-size: 25.11px;
    font-weight: 800;
    line-height: 30px;  
    margin-bottom: 5rem;
    cursor: pointer;
}

.gpt3_blog-container_article-content p:last-child
{
    cursor: pointer;
}

@media screen and (max-width:550px)
{
    .gpt3_blog-container_article-content
    {
        font-size: 18px;
        line-height: 25px;
    }    
}