.gpt3_blog
{
    display: flex;
    flex-direction: column;
}

.gpt3_blog-heading
{
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}

.gpt3_blog-heading h1
{
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -2.5px;
}

.gpt3_blog-container
{
    display: flex;
    flex-direction: row;
}

.gpt3_blog-container_groupA
{
    flex: 0.75;
    margin-right: 2rem;
}

.gpt3_blog-container_groupB
{
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
}

@media screen and (max-width:990px)
{
    .gpt3_blog-container
    {
        flex-direction: column-reverse;
    }    

    .gpt3_blog-container_groupA
    {
        margin: 2rem 0;
    }

    .gpt3_blog-container_groupA .gpt3_blog-container_article
    {
        width: 48%;
    }
    
    .gpt3_blog-container_groupA .gpt3_blog-container_article-image
    {
        height: 250px;
    }
}

@media screen and (max-width:700px)
{
    .gpt3_blog-container_groupB
    {
        grid-template-columns: repeat(1, 1fr);
    }   
    
    .gpt3_blog-container_groupA .gpt3_blog-container_article
    {
        width: 100%;
    }

    .gpt3_blog-heading h1
    {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width:550px)
{
    .gpt3_blog-heading h1
    {
        font-size: 34px;
        line-height: 42px;
    }
}